.buttonGoBack {
  background-color: orangered;
  color: white;
  outline: none;
  border: none;
  border-radius: 5px;
  font-size: 25px;
  padding: 10px;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  margin-bottom: 15px;
}
.buttonGoBack:focus,
.buttonGoBack:hover {
  cursor: pointer;
  opacity: 0.8;
}
